<script>
export default {
  data() {
    return {
      user: {
        email: '',
        pwd: '',
      },
      loginErrorMes: [],
      loginError: false,
    }
  },
  methods: {
    signin() {
      const api = `${process.env.VUE_APP_CHECK_API_URL}/api/v1/traffic/Login`
      const vm = this
      this.$store.dispatch('updateLoading', true)
      this.$http
        .post(api, vm.user)
        .then((res) => {
          if (res.data.success && res.data.auth) {
            //const token = res.data.token;
            //document.cookie = `trafficToken=${token} max-age=`;//把cookie寫入
            vm.$store.dispatch('updateLoading', false)
            vm.$router.push({ name: 'Admin' })
          }
        })
        .catch((err) => {
          const errMes = err.response.data.message.message
          vm.loginErrorMes.splice(0, 1, errMes)
          vm.loginError = true
          console.log(vm.loginErrorMes)
          vm.$store.dispatch('updateLoading', false)
          if (vm.$route.path !== '/Login') {
            vm.$router.push({ name: 'Login' })
          }
        })
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading
    },
  },
}
</script>

<template>
  <div class="container">
    <loading :active.sync="isLoading"></loading>
    <validation-observer v-slot="{ invalid, handleSubmit }"
                         tag="div">
      <form @submit.prevent="handleSubmit(signin)">
        <div class="mb-3">
          <validation-provider rules="required-SignUp-Login-Email|email"
                               v-slot="{ errors, classes, passed }"
                               tag="div">
            <!--rules帶上驗證的規則-->
            <!--v-slot 稱為插槽，可把外部元件的data傳到裡面slot的內容-->
            <label for="exampleInputEmail1"
                   class="form-label">Email address</label>
            <input type="email"
                   v-model="user.email"
                   class="form-control"
                   :class="classes"
                   id="exampleInputEmail1"
                   aria-describedby="emailHelp"
                   autocomplete="username" />
            <span class="invalid-feedback">{{ errors[0] }}</span>
            <span v-if="passed"
                  class="valid-feedback">Valid email address</span>
          </validation-provider>
        </div>
        <div class="mb-3">
          <validation-provider rules="required-SignUp-Login-Password|password"
                               v-slot="{ errors, classes }"
                               tag="div">
            <label for="exampleInputPassword1"
                   class="form-label">Password</label>
            <input type="password"
                   v-model="user.pwd"
                   class="form-control"
                   :class="classes"
                   id="exampleInputPassword1"
                   autocomplete="current-password" />
            <span class="invalid-feedback">{{ errors[0] }}</span>
            <span class="text-danger"
                  v-if="loginError">{{ loginErrorMes[0] }}</span>
          </validation-provider>
        </div>
        <div class="mb-3 form-check">
          <input type="checkbox"
                 class="form-check-input"
                 id="exampleCheck1" />
          <label class="form-check-label"
                 for="exampleCheck1">Check me out</label>
        </div>
        <div class="form-group">
          <div class="col-md-12 control">
            <div class="alert alert-info"
                 role="alert">
              Don't have an account!
              <a class="alert-link"
                 href="/Signup">
                Sign Up Here
              </a>
            </div>
          </div>
        </div>
        <button type="submit"
                class="btn btn-primary"
                :disabled="invalid">Submit</button>
      </form>
    </validation-observer>
  </div>
</template>
