<script>
export default {
  data() {
    return {
      //防止變數污染要把資料寫在return裡(只在當前component生效)
      time: 5,
      timer: null,
      allLed: true,
      isEnable: false,
      isDisable: false,
    }
  },
  methods: {
    triggerLed() {
      this.allLed = true
      this.$socket.emit('ledOnEvent', this.allLed)
      this.isEnable = true
    },
    closeLed() {
      this.allLed = false
      this.$socket.emit('ledOffEvent', this.allLed)
      this.isDisable = true
    },
  },
  watch: {
    isDisable() {
      if (!this.isDisable) {
        return //防止連續點擊時，倒數變快(函式節流:throttle)
      }
      const vm = this
      vm.timer = setInterval(() => {
        vm.time--
        if (this.time === 0) {
          clearInterval(vm.timer)
          vm.time = 5
          vm.isEnable = false
          vm.isDisable = false
        }
      }, 1000)
    },
  },
  beforeDestory() {
    console.log('觸發beforeDestory')
    clearInterval(this.timer)
  },
}
</script>

<template>
  <div class="container">
    <h1>Traffic-Control-System</h1>
    <button :class="['btn btn-primary', { collapsed: !isEnable }]"
            @click="triggerLed"
            :disabled="isEnable"
            type="button"
            data-toggle="collapse"
            data-target="#collapseExample"
            aria-expanded="false"
            aria-controls="collapseExample">
      開啟
    </button>
    <button :class="['btn btn-danger', { collapsed: !isDisable }]"
            @click="closeLed"
            :disabled="isDisable"
            type="button"
            data-toggle="collapse"
            data-target="#collapseExample"
            aria-expanded="false"
            aria-controls="collapseExample">
      關閉
    </button>

    <div :class="['collapse', { show: isDisable }]"
         id="collapseExample">
      <div class="card card-body">
        <span>{{ time }}</span>
      </div>
    </div>
  </div>
</template>
