import Vue from 'vue';
import axios from 'axios';
import App from './App.vue';
import router from './router';
import VueAxios from 'vue-axios'; //讓你能用vue.$http等同vue.axios
import VueSocketIO from 'vue-socket.io';
import BootstrapVue from 'bootstrap-vue';
import Loading from 'vue-loading-overlay';
import 'bootstrap/dist/css/bootstrap.css';
import store from './store';
// Import stylesheet
import 'vue-loading-overlay/dist/vue-loading.css';
import {  ValidationObserver, ValidationProvider, extend, configure } from 'vee-validate';
import { required, email, min } from 'vee-validate/dist/rules';  // 按需求導入規則
Vue.config.productionTip = false;
axios.defaults.withCredentials = true; //開啟認證

extend('email', email);
extend('required', required);
extend('confirmPassword', {
  ...required,//官方自帶的驗證規則
  message: 'Please confirm your password again'
})
extend('min', min);
extend('required-SignUp-Name', {
  ...required,
  message: 'Please fill in your name'
})

extend('required-SignUp-Login-Email', {
  ...required,
  message: 'Please fill in your Email'
})

extend('required-SignUp-Login-Password', {
  ...required,
  message: 'Please fill in your Password'
})
extend('password', {
  validate(value) { //自訂義的驗證規則
    const expression = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,}$/;
    const pwdExp = new RegExp(expression);//建立正則表達式物件
    if(pwdExp.test(value)==true) { //通過正則表達式的驗證便回傳true。
      return true
    } else {
      return false
    }
  },
  message: `The password should be at least 8 yards in length,and mixed uppercase and lowercase English letters and numbers`
})
extend('required-SignUp-Login-PasswordConfirm', {
  ...required,
  params: ['pwd'],
  validate(value, { pwd }){
    return value === pwd;
  },
  message: `The password you entered for the second time is different, please confirm again`
})
//自訂驗證規則

//註冊全域元件
Vue.component('loading', Loading);
Vue.component('ValidationProvider', ValidationProvider);
Vue.component('ValidationObserver', ValidationObserver);

Vue.use(
  new VueSocketIO({
    debug: true,
    //connection: `http://127.0.0.1:3000`
    connection: `https://traffic-control-system.herokuapp.com:80`
  })
)
  .use(router)
  .use(BootstrapVue)
  .use(VueAxios, axios);

  // Class 設定檔案
  configure({
    classes: {
      valid: 'is-valid',//針對bootstrap所進行驗證的class name的設定
      invalid: 'is-invalid'
    }
  });
  
router.beforeEach((to, from, next) => {
  console.log('驗證');
  if(to.meta.requiresAuth === true) {
    const api = `${process.env.VUE_APP_CHECK_API_URL}/api/v1/traffic/Check`;
    axios.post(api).then((res) => {//執行環境在router下，故不能使用this，this是在vue元件內才能使用(所以this.$http要換成axios)
      console.log(res);
      if(res.data.success) {
        next();
      } else {
        next({
          path: '/Login'
        })
      }
    });
  } else {
    next();
  }
});


new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');

console.log('環境: ', process.env);