<script>
import { Bar } from 'vue-chartjs'
export default {
  extends: Bar,
  props: {
    //需要使用props(從父元件把資料傳到子元件)，直接用this指向當前元件定義的data，無法把值新增進去。
    sdata: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      loaded: false,
      sensordata: {
        labels: ['當前車子數量'], //x軸的數據(key)
        datasets: [
          {
            label: '車子統計數量', //x軸標籤名稱
            data: this.sdata, //y軸的數據(偵測到的車子數量)
            backgroundColor: ['rgba(54, 162, 235, 0.2)'],
            borderColor: '#fff',
            borderWidth: 1, // 外框線寬度
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          labels: {
            fontColor: 'white',
            fontSize: 15,
          },
        },
        scales: {
          yAxes: [
            {
              ticks: {
                min: 0,
                max: 50,
                fontColor: 'white',
                fontSize: 15,
                stepSize: 5, // y軸上的間隔
              },
              gridLines: {
                display: true,
                color: '#4C4C4C',
              },
            },
          ],
          xAxes: [
            {
              ticks: {
                fontColor: 'white',
                fontSize: 15,
              },
              gridLines: {
                display: true,
                color: '#4C4C4C',
              },
            },
          ],
        },
      },
    }
  },
  //在template render成HTML後呼叫
  mounted() {
    this.renderForceSensitiveResistorChart()
  },
  methods: {
    renderForceSensitiveResistorChart() {
      this.renderChart(this.sensordata, this.options)
    },
  },
  watch: {
    sdata() {
      if (localStorage.getItem('sensordata')) {
        console.log('watch:localStorage')
        this.$data._chart.destroy()
        this.renderForceSensitiveResistorChart()
      }
      console.log('out:localStorage')
      this.$data._chart.destroy()
      this.renderForceSensitiveResistorChart()
    },
  },
}
//this.$data._chart.config.data.datasets[0].data
</script>