<script>
export default {
  data() {
    return {
      user: {
        name: '',
        email: '',
        pwd: '',
      },
      confirmPassword: '',
    }
  },
  methods: {
    signup() {
      const api = `${process.env.VUE_APP_CHECK_API_URL}/api/v1/traffic/Signup`
      const vm = this
      this.$store.dispatch('updateLoading', true)
      this.$http.post(api, vm.user).then((res) => {
        if (res.data.success) {
          vm.$store.dispatch('updateLoading', false)
          vm.$router.push({ name: 'Login' })
        } else {
          vm.$store.dispatch('updateLoading', false)
          vm.$router.push({ name: 'Signup' })
        }
      })
    },
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading
    },
  },
}
</script>

<template>
  <div class="container">
    <loading :active.sync="isLoading"></loading>
    <validation-observer v-slot="{ invalid, handleSubmit }"
                         tag="div">
      <div class="row">
        <div class="myform form ">
          <form @submit.prevent="handleSubmit(signup)">
            <validation-provider rules="required-SignUp-Name"
                                 v-slot="{ errors, classes }"
                                 tag="div"
                                 class="mb-3">
              <label for="exampleInputEmail1"
                     class="form-label">Sign-up</label>
              <input type="text"
                     v-model="user.name"
                     class="form-control my-input"
                     :class="classes"
                     placeholder="Name" />
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <validation-provider rules="required-SignUp-Login-Email|email"
                                 v-slot="{ errors, classes }"
                                 tag="div"
                                 class="mb-3">
              <input type="email"
                     v-model="user.email"
                     class="form-control my-input"
                     :class="classes"
                     placeholder="Email"
                     autocomplete="username">
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <!--validation-provider這邊的name當參數，傳入extend與PasswordConfirm比對-->
            <validation-provider rules="required-SignUp-Login-Password"
                                 v-slot="{ errors, classes }"
                                 tag="div"
                                 name="pwd"
                                 class="mb-3">
              <input type="password"
                     v-model="user.pwd"
                     class="form-control"
                     :class="classes"
                     placeholder="Password"
                     autocomplete="current-password">
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <validation-provider rules="confirmPassword|required-SignUp-Login-PasswordConfirm:@pwd"
                                 v-slot="{ errors, classes }"
                                 tag="div"
                                 class="mb-3">
              <input type="password"
                     class="form-control"
                     :class="classes"
                     placeholder="PasswordConfirm"
                     v-model="confirmPassword">
              <span class="invalid-feedback">{{ errors[0] }}</span>
            </validation-provider>
            <div class="text-center">
              <button type="submit"
                      class="btn btn-primary"
                      :disabled="invalid">Create Your Account</button>
            </div>
          </form>
        </div>
      </div>
    </validation-observer>
  </div>
</template>
