<script>
export default {
  data() {
    return {
      menuBar: false,
    }
  },
  methods: {
    isOpen: function () {
      this.menuBar = !this.menuBar
    },
  },
}
</script>

<template>
  <header>
    <div @click="isOpen"
         :class="['menu-collapased', {'menu-expanded': menuBar}]">
      <div class="bar"></div>
      <nav>
        <ul>
          <li>
            <router-link to="/">Home</router-link>
          </li>
          <li>
            <router-link to="/Login">Login</router-link>
          </li>
          <li>
            <router-link to="/Statistics">Statistics</router-link>
          </li>
        </ul>
      </nav>
    </div>
  </header>
</template>