<script>
import forceSensitiveResistor from '../components/Chart'
export default {
  components: { forceSensitiveResistor },
  data() {
    return {
      sensordata: [],
    }
  },
  //created是在template render成HTML前呼叫
  created() {
    if (localStorage.getItem('sensordata')) {
      try {
        this.sensordata.splice(
          0,
          1,
          JSON.parse(localStorage.getItem('sensordata')[0])
        )
      } catch (err) {
        console.log(err)
      }
    }
    this.receiveSensorValue()
  },
  methods: {
    receiveSensorValue() {
      console.log(this.sensordata)
      this.sockets.subscribe(
        //訂閱事件，接收壓力感測器的值
        'ForceSensitiveResistorEvent',
        async function (data) {
          //replaces 1 element at index 4
          await this.sensordata.splice(0, 1, data)
          this.saveSensorData()
        }
      )
    },
    saveSensorData() {
      const parsed = JSON.stringify(this.sensordata)
      localStorage.setItem('sensordata', parsed)
      if (this.sensordata) {
        this.sockets.unsubscribe('ForceSensitiveResistorEvent') //訂閱事件記得要移除，要不然明明感測一次卻會收到多條訊息
        this.receiveSensorValue()
      }
    },
  },
}
</script>

<template>
  <div class="container">
    <force-sensitive-resistor :sdata='this.sensordata' />
  </div>
</template>

